const createRestaurantDetailTemplate = (makanan) => `
  <h2 class="resto__title">${makanan.name || '-'}</h2>
  <img class="resto__poster" src="${
  makanan.image
}" alt="${makanan.name}" />
  <div class="resto__info">
    <h3>Information</h3>
    <h4>publisher</h4>
    <p>${makanan.publisher}</p>
    <h4>Rating</h4>
    <p>${makanan.rating}</p>
  </div>
  <div class="resto__overview">
    <h3>Description</h3>
    <p>${makanan.description}</p>
    <h3>waktu</h3>
    <p>${makanan.waktu}</p>
    <h3>tingkatSulit</h3>
    <p>${makanan.tingkatSulit}</p>

    <h2 tabindex="0" id="resto-detail-form-review-title" class="teks-review"><span>Reviews</span></h2>
    <div tabindex="0" class="detail-review">
      ${makanan.reseps
    .map(
      (resep) => `
        <div class="detail-review-item">
          <div class="review-header">
            <p class="review-date">${resep.bahan}</p>
          </div>
          <div class="review-body">
            ${resep.caraMasak}
          </div>
        </div>
      `,
    )
    .join('')}
    </div>
  </div>
`;

const createRestaurantItemTemplate = (makanan) => `
  <div class="resto-item">
    <div class="resto-item__header">
      <img class="lazyload resto-item__header__poster" data-src="${makanan.image}" alt="${
  makanan.name || '-'
}">
      <div class="resto-item__header__rating">
        <p>⭐️<span class="resto-item__header__rating__score">${
  makanan.rating || '-'
}</span></p>
      </div>
    </div>
    <div class="resto-item__content">
    <p class="resto_city">${makanan.publisher || '-'}</p>
      <h3 class="resto__title"><a href="/#/detail/${makanan.id}">${makanan.name || '-'}</a></h3>
      <p>${makanan.tingkatSulit || '-'}</p>
      <p>${makanan.waktu || '-'}</p>
      <p>${makanan.description || '-'}</p>
      
    </div>

  </div>
`;
const createDaerahDetailTemplate = (daerah) => `
  <img class="resto__poster" src="${
  daerah.pictureId
}" alt="${daerah.name}" />

    <h2 tabindex="0" id="resto-detail-form-review-title" class="teks-review"><span>Reviews</span></h2>
    <div tabindex="0" class="detail-review">
      ${daerah.makanans
    .map(
      (makanan) => `
        <div class="detail-review-item">
          <div class="review-header">
            <p class="review-date">${makanan.name}</p>
          </div>
        </div>
      `,
    )
    .join('')}
    </div>
  </div>
`;

const createDaerahItemTemplate = (daerah) => `
  <div class="resto-item">
    <div class="resto-item__header">
      <img class="lazyload resto-item__header__poster" data-src="${daerah.pictureId}" alt="${
  daerah.name || '-'
}">    
<div class="resto-item__content">
<h3 class="resto__title"><a href="/#/detail-daerah/${daerah.daerahId}">${daerah.name || '-'}</a></h3>
    </div>
    </div>
  </div>
`;

const createLikeRestaurantButtonTemplate = () => `
  <button aria-label="like this restaurant" id="likeButton" class="like">
    <i class="fa fa-heart-o" aria-hidden="true"></i>
  </button>
`;

const createUnlikeRestaurantButtonTemplate = () => `
  <button aria-label="unlike this restaurant" id="likeButton" class="like">
    <i class="fa fa-heart" aria-hidden="true"></i>
  </button>
`;

export {
  createRestaurantItemTemplate,
  createRestaurantDetailTemplate,
  createDaerahItemTemplate,
  createDaerahDetailTemplate,
  createLikeRestaurantButtonTemplate,
  createUnlikeRestaurantButtonTemplate,
};
